<template>
   <div class="content-wrapper">
    <div class="loading-container" v-if="isLoading">
      <!--<me-loading/>-->
      <div class="loading-wrapper">
        <me-loading/>
      </div>
    </div>
    <me-scroll
      ref="scroll"
      @scrollEnd="scrollEnd"
      @scroll="scroll"
    >
      <div class="content">
        <div class="pic" v-if="content.banner">
          <a :href="content.banner.linkUrl" class="pic-link">
            <img @load="updateScroll" :src="content.banner.picUrl" alt="" class="pic-img">
          </a>
        </div>
        <div class="section" v-for="(section, index) in content.data" :key="index">
          <h4 class="section-title">{{section.name}}</h4>
          <ul class="section-list">
            <li
              class="section-item"
              v-for="(item, i) in section.itemList" :key="i"
            >
              <a :href="item.linkUrl" class="section-link">
                <p class="section-pic">
                  <img v-lazy="item.picUrl" alt="" class="section-img" />
                </p>
                <p class="section-name">{{item.name}}</p>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </me-scroll>
    <div class="g-backtop-container">
      <me-backtop @backtoTop="backToTop" :visible="visible"/>
    </div>
  </div>
</template>

<script>
  import MeLoading from 'base/loading';
  import MeScroll from 'base/scroll';
  import MeBacktop from 'base/backtoTop';
  import {getCategoryContent} from 'api/category';
  import storage from 'assets/js/storage';
  import {CATEGORY_CONTENT_KEY, CATEGORY_CONTENT_UPDATE_TIME_INTERVAL} from './config';

  export default {
    name: 'CategoryContent',
    components: {
      MeLoading,
      MeScroll,
      MeBacktop
    },
    props: {
      curId: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        visible: false,
        content: {},
        isLoading: false
      };
    },
    watch: {
      curId(id) {
        this.isLoading = true;
        this.getContent(id).then(() => {
          this.isLoading = false;
          this.backToTop(0);
        });
      }
    },
    methods: {
      backToTop(speed) {
        this.$refs.scroll && this.$refs.scroll.scrollToTop(speed);
      },
      updateScroll() {
        this.$refs.scroll && this.$refs.scroll.update();
      },
      scroll(translate, swiper) {
        this.visible = translate < 0 && Math.abs(translate) > swiper.height;
      },
      scrollEnd(translate, swiper) {
        this.visible = translate < 0 && Math.abs(translate) > swiper.height;
      },
      getContent(id) {
        let contents = storage.get(CATEGORY_CONTENT_KEY); // 获取整个对象
        let updateTime;
        let curTime = new Date().getTime();
        if (contents && contents[id]) { // 如果有该缓存对象
          updateTime = contents[id].updateTime || 0; // 获取缓存对象的最近更新时间 如果无则为0
          if (curTime - updateTime < CATEGORY_CONTENT_UPDATE_TIME_INTERVAL) { // 如果小于规定的缓存时间
            return this.getContentByLocalStorage(contents[id]); // 返回promise对象
          } else { // 缓存超时
            return this.getContentByHttp(id).then(() => {
              this.updateLocalStorage(contents, id, curTime); // 发送完http请求以后更新缓存
            }); // 返回promise对象
          }
        } else { // 不存在缓存对象
          return this.getContentByHttp(id).then(() => {
            this.updateLocalStorage(contents, id, curTime); // 发送完http请求以后更新缓存
          }); // 返回promise对象
        }
      },
      getContentByLocalStorage(content) {
        this.content = content.data;
        return Promise.resolve(); // ⭐return Promise.resolve 执行then中的代码
      },
      getContentByHttp(id) {
        return getCategoryContent(id).then(data => {
          return new Promise(resolve => {
            if (data) {
              console.log(data.content);
              this.content = data.content;
              resolve();
            }
          });
        });
      },
      updateLocalStorage(contents, id, curTime) {
        contents = contents || {}; // 避免缓存为null 也就是第一次什么也没有
        contents[id] = {}; // 清空上一次缓存
        contents[id].data = this.content; // 为当前id添加data
        contents[id].updateTime = curTime; // 添加时间戳
        storage.set(CATEGORY_CONTENT_KEY, contents); // 更新缓存
      }

    }
  };
</script>

<style lang="scss" scoped>
@import "~assets/scss/mixins";

  .content-wrapper {
    position: relative;
    height: 100%;
  }

  .loading-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: $category-popup-z-index;
    @include flex-center();
    width: 100%;
    height: 100%;
    /*background-color: $modal-bgc;*/

    .mine-loading {
      color: #fff;
      font-size: 14px;
    }
  }
  .loading-wrapper {
    width: 50%;
    padding: 30px 0;
    background-color: $modal-bgc;
    border-radius: 4px;
  }

  .content {
    padding: 10px;
  }

  .pic {
    margin-bottom: 12px;

    &-link {
      display: block;
    }

    &-img {
      width: 100%;
    }
  }

  .section {
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }

    &-title {
      height: 28px;
      line-height: 28px;
      color: #080808;
      font-weight: bold;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      background-color: #fff;
      padding: 10px 10px 0;
    }

    &-item {
      width: (100% / 3);
    }

    &-link {
      display: block;
    }

    &-pic {
      position: relative;
      width: 80%;
      padding-bottom: 80%;
      margin: 0 auto;
    }

    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &-name {
      height: 36px;
      line-height: 36px;
      text-align: center;
      @include ellipsis();
    }
  }

  .g-backtop-container {
    bottom: 10px;
  }

</style>



